<template>
  <div></div>
</template>

<script>
export default {
  name: "SlackConnection",

  beforeRouteEnter(to, from, next) {
    next(() => {
      const authCode = to.query.code;
      // console.log(authCode);

      if (authCode) {
        localStorage.setItem("slackSignToken", authCode);
        window.close();
      }
    });
  },
};
</script>

